<template>
  <div class="hackathons-container" v-if="hackathonsList">
    <div class="hackathons-filling">
      <a-table :columns="columns" :data-source="hackathonsList">
        <template #bodyCell="{column,record}">
          <div class="" v-if="column?.key==='actions'">
            <a-button @click="editHackathon(record)">Редактировать</a-button>
          </div>
        </template>
      </a-table>
    </div>

  </div>
</template>


<script>
import {useStore} from "vuex";
import {computed} from "vue";
import {useRouter} from "vue-router";

export default {
  name: 'HackathonsListPage',
  setup() {
    const store = useStore();
    store.dispatch('fetchHackathonsList');
    const router = useRouter();
    const hackathonsList = computed(() => store.getters.hackathonsList);
    const columns = [
      {
        title: 'Название',
        dataIndex: 'title',
        ellipsis: true
      },
      {
        title: 'Описание',
        dataIndex: 'description',
        ellipsis: true
      },
      {
        title: 'Дата начала',
        dataIndex: 'startDate',
      },
      {
        title: 'Дата окончания',
        dataIndex: 'startDate',
      },
      {
        title: 'Статус',
        key: 'status',
      },
      {
        title: 'Ссылка',
        dataIndex: 'tableId',
        ellipsis: true
      },
      {
        title: 'Действия',
        key: 'actions',
      },
    ];


    const editHackathon = (hackathon) => {
      router.push({name: 'hackathonEditor', params: {hackathonId: hackathon?.id}});
    }

    return {
      hackathonsList,
      columns,
      editHackathon
    }

  }
}
</script>


<style scoped>
.hackathons-container {
  padding: 24px;
}

.hackathons-filling {
  padding: 24px;
  box-shadow: 0px 2px 8px 0px #00000026;
}
</style>